.status{
color: white;
border-radius: 5px;
height: 20%;
line-height: 4em;
width: 100%;
}

.status h1{
  color: #fff;
  background-color: rgb(73, 9, 110);
  font-size: 20px;
  border-radius: 0 0 5px 5px;
  text-align: center;

}


.status h2{
  color: #FF0088;
  text-align: center;
  border-bottom: 1px solid rgb(223, 223, 223);
}


.status h3{
  color: #FF0088;
  text-align: center;
  border-bottom: 1px solid rgb(223, 223, 223);
}