.product{
  margin-top: 30px;
border-radius: 5px;
   float: left;
   box-shadow: 2px 1px 2px #e4e4e4;
  margin-bottom: 20px;
  margin-right: 50px;
  padding: 20px 30px 30px 20px ;
  text-align: center;
}


.product-name{
  color:rgb(26, 18, 68);
  font-weight: 800;
  font-size: 30px;
}


.main-btn{
  background-color: rgb(255, 23, 62);
  color: white;

border: none;
padding: 10px;
border-radius: 5px;
}


.main-btn:hover{
  background-color: rgb(197, 3, 35);
  color: white;
  cursor: pointer;
}